.instagram__title {
  display: flex;
  margin-bottom: 111px;
  h2 {
    font-family: "NeutralFace-Bold";
    font-size: 48px;
    line-height: 106%;
    color: #212121;
    margin-right: 90px;
    width: 344px;
  }
  a {
    font-family: "PTRootUIWebRegular";
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 106%;
    color: #212121;
    padding-top: 12px;
    transition: 0.2s;
    &:hover {
      opacity: 0.7;
    }
    &:active {
      opacity: 0.5;
    }
  }
}

.instagram__list {
  display: flex;
  justify-content: space-between;
  // img {
  //   max-width: 100%;
  //   height: auto;
  // }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}
